body{
  margin:0;
  padding: 0;
  box-sizing: border-box;
  font-family: poppins;
}
.App{
  min-height: 100vh;
}
.darkMode{
  background-color: #040537;
}
.lightMode{
  background-color: #FFF;
}

.container{
  max-width: 960px;
  margin: 0px auto;
  padding: 0px 15px;
}

.notes-list{
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
}

.note{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: aqua;
  padding: 1rem;
  border-radius: 5px;
  min-height: 150px;
  max-width: 100%;
  white-space: pre-wrap;
}


.note-footer{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.note-footer small{
  color: #666;
  font-size: 12px;
}
.fa-trash{
  cursor: pointer;
  transition: 0.5s;
}
.fa-trash:hover{
  color: #f22;
}

.note.new{
  background-color: #9F9;
}
.new textarea{
  background-color: transparent;
  resize: none;
  border: none;
  outline: none;
  color: #000;
}
button.save{
  background-color: #7f7;
  color: #000;
  border: none;
  outline: none;
  padding: 8px 10px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 500;
}
button.save:hover{
  background-color: #008000;
  color: #FFF;
}

.search{
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 5px 10px;
  gap:1rem;
  background-color: #DDD;
  margin-bottom: 1.5em;
}
.search input{
  background-color: transparent;
  width: 100%;
  padding: 10px 0px;
  border: none;
  outline: none;
  font-weight: 400;
}
.search .fa-search{
  margin-left: 15px;
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Togglesave{
  height: 30px;
}
.darkModeToggle{
  cursor: pointer;
  font-size: 32px;
}